.formulario {
    width: 95vw;
    margin: auto;
    max-width: 900px;
}
.nova-despesa__controles {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
    justify-content: center;
}

.nova-despesa__controles label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
}

.nova-despesa__controles input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 10rem;
    max-width: 100%;
}

.nova-despesa__acoes {
    text-align: center;
}
