.item-despesa {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
    margin: 1rem 0;
    background-color: #dcfbff;
    /* width: 90vw; */
    margin: 2% auto;

    /* Removendo pois foram extraidos para Card.css */
    /* box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    border-radius: 8px; */
}

.item-despesa:hover {
  box-shadow: black 0 0 20px;
}

.item-despesa__descricao {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
}

.item-despesa h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: black;
}

.item-despesa__preco {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: darkblue;
    border: 3px solid black;
    padding: 0.5rem;
    border-radius: 12px;
}

@media (min-width: 580px) {
    .item-despesa__descricao {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1;
    }

    .item-despesa__descricao h2 {
        font-size: 1.25rem;
    }

    .item-despesa__preco {
        font-size: 1.25rem;
        padding: 0.5rem 1.5rem;
    }
}
